import {React} from 'react'
import OS from '../../assets/os.svg';
import transaction from '../../assets/transaction.svg'
import website from '../../assets/website.svg'
import analytics from '../../assets/analytics.svg'
import circular from '../../assets/circular-arrows.svg'
import statistics from '../../assets/statistics.svg'
import fast from '../../assets/fast.svg'
import { useHistory } from 'react-router';

const ApiCatalog = () =>{
    const history = useHistory()
    return(
        <>       
        <div className="card pr-3 pl-4 py-5 d-flex flex-row" style={{height:'100%', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                <div className="ml-5 mt-3 mb-3">
                <img style={{width:'20vw'}} class="logo" src={OS} alt="Icon Telp"/>
                </div>   
                <div className="ml-5 mt-3 mb-3 d-flex flex-column">
                    <div><a style={{fontWeight:'300', fontSize:'2.4rem'}}>Satu Pintu untuk Segala Kebutuhan Integrasimu.</a></div>
                    <div role="button" className="mt-4"><a style={{fontWeight:'500', fontSize:'1.4rem', borderBottom: '1px solid #000'}}>Ajukan Permintaan</a></div>
                </div>                               
        </div>
        <div className="d-flex flex-row">
            <div className="card pr-3 pl-4 py-3 d-flex flex-column mr-4" style={{borderRadius:'12px', width:'40%', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                <div><a style={{fontWeight:'600', fontSize:'1.7rem'}}>Apa Itu API INSW ?</a></div>
                <div className="d-flex flex-row mt-3">
                <a style={{fontWeight:'200', fontSize:'1rem'}}>
                    API (<i>Application Programming Interface</i>) INSW adalah sebuah layanan dari INSW untuk memberikan kemudahan untuk kamu dalam mengakses layanan-layanan yang ada di INSW secara langsung dari platform kamu melalui mekanisme <i>host2host</i>
                </a>
                </div>              
            </div>
            <div className="card pr-4 pl-4 py-3 d-flex flex-column" style={{borderRadius:'12px', width:'60%', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                <div><a style={{fontWeight:'600', fontSize:'1.7rem'}}>Kenapa Harus API INSW ?</a></div>
                <div className="d-flex flex-row justify-content-center mt-3">
                    <div className="card px-3 py-3 d-flex flex-column mr-3 clickable" style={{minWidth:'16vw',maxWidth:'16vw', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                        <div className="d-flex flex-row justify-content-center mb-2"><img style={{width:'3vw'}} class="logo" src={circular} alt="Icon Telp"/></div>
                        <div><a style={{fontWeight:'800', fontSize:'15px'}}>Otomasi</a></div>
                        <div>
                            <a style={{fontWeight:'300'}}>Akses informasi dan transaksi bisnis langsung dari platform kamu untuk kembangkan bisnismu.</a>
                        </div>
                    </div>
                    <div className="card px-3 py-3 d-flex flex-column mr-3 clickable" style={{minWidth:'16vw',maxWidth:'16vw', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                        <div className="d-flex flex-row justify-content-center mb-2"><img style={{width:'3vw'}} class="logo" src={statistics} alt="Icon Telp"/></div>
                        <div><a style={{fontWeight:'800', fontSize:'15px'}}>Lebih Efisien</a></div>
                        <div>
                            <a style={{fontWeight:'300'}}>Otomasi proses bisnismu sehingga biaya operasionalmu menjadi lebih efisien.</a>
                        </div>
                    </div>
                    <div className="card px-3 py-3 d-flex flex-column clickable" style={{minWidth:'16vw',maxWidth:'16vw', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                        <div className="d-flex flex-row justify-content-center mb-2"><img style={{width:'3vw', color:'red'}} class="logo" src={fast} alt="Icon Telp"/></div>
                        <div><a style={{fontWeight:'800', fontSize:'15px'}}>Lebih Cepat</a></div>
                        <div>
                            <a style={{fontWeight:'300'}}>Ajukan pengajuan dan cek informasi secara lebih cepat langsung dari platform kamu</a>
                        </div>
                    </div>                                        
                </div>                 
            </div>                        
        </div>        
        {/* <div className="card pr-3 pl-4 py-3 d-flex flex-column mr-4" style={{borderRadius:'12px', width:'100%', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}> */}
        {/* <div className="mb-4"><a style={{fontWeight:'600', fontSize:'1.7rem'}}>Layanan API INSW</a></div> */}
        <div className="card pr-3 pl-4 py-3 d-flex flex-column mr-4 d-flex flex-row justify-content-center" style={{borderRadius:'12px', width:'100%', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            <div className="mb-4"><a style={{fontWeight:'600', fontSize:'1.7rem'}}>Layanan API INSW</a></div>            
            <div className="d-flex flex-row justify-content-center">
        <div role="button" onClick={()=>history.push('/katalogapi/transaksi')} className="card px-3 py-5 mr-4 clickable" style={{minHeight:'25vh',minWidth:'29vw', maxWidth:'28vw', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            <div className="d-flex flex-row justify-content-center">
                <div className="mr-3 d-flex flex-column">
                    <div><a style={{fontWeight:'600', fontSize:'20px'}}>Layanan API <a style={{fontWeight:'780'}}>Transaksi</a></a></div>
                    <div><a>Meliputi Layanan Perijinan, Kepabeanan, dan Perpajakan</a></div>
                </div>                                
                <div className="d-flex justify-content-end"><img style={{width:'4vw'}} class="logo" src={transaction} alt="Icon Telp"/></div>
            </div>
        </div>
        <div role="button" className="card px-3 py-5 mr-4 clickable" style={{minHeight:'25vh',minWidth:'29vw', maxWidth:'28vw', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            <div className="d-flex flex-row justify-content-center">
                <div className="mr-3 d-flex flex-column">
                    <div><a style={{fontWeight:'600', fontSize:'20px'}}>Layanan API <a style={{fontWeight:'780'}}>Informasi</a></a></div>
                    <div><a>Meliputi Layanan INTR, Tracking Status, Referensi, dan lain sebagainya</a></div>
                </div>                
                <div className="d-flex justify-content-end"><img style={{width:'4vw'}} class="logo" src={website} alt="Icon Telp"/></div>
            </div>
        </div>
        <div role="button" className="card px-3 py-5 clickable" style={{minHeight:'25vh',minWidth:'29vw', maxWidth:'28vw', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            <div className="d-flex flex-row justify-content-center">
                <div className="mr-3 d-flex flex-column">
                    <div><a style={{fontWeight:'600', fontSize:'20px'}}>Layanan API <a style={{fontWeight:'780'}}>Pelaporan</a> </a></div>
                    <div><a>Meliputi Layanan Monitoring Kuota, Realisasi Perijinan, dan lain sebagainya </a></div>
                </div>                
                <div className="d-flex justify-content-end"><img style={{width:'4vw'}} class="logo" src={analytics} alt="Icon Telp"/></div>
            </div>
        </div>                   
            </div>                         
        </div>                        
        {/* </div>         */}
        </>
    )
}

export default ApiCatalog